.navbar{
    display: flex;
    justify-content:space-around;
    padding: 16px;
    box-shadow: 0 1px 3px -2px black;

}

.nav-logo{
    display: flex;
    align-items: center;
    gap: 10px;
}

/*.nav-logo p {
    color: aqua;
    font-size: larger;
}*/



.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
    color: black;
    font-size: 20px;
}


.nav-menu li{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3px;
    cursor: pointer;
}

.nav-menu:hover li{
    border-radius: 60px;  
    background-color: yellow;  
}

.nav-login-cart{
    display:flex;
    align-items: center;
    gap: 45px;
}


.nav-cart-count{
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -35px;
    margin-left: -55px;
    border-radius: 11px;
    font-size: 14px;
    background-color: red;
    color: white;

}




